$grey: #d9d9d9;

.body{
    &__grey{
        background-color: rgba($grey, 20%);
    }
    &__lightBlue{
        background-color: #05cbcb;
    }
}

.loader{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

button{
    cursor: pointer;
}
